<script>
import {getDoctorList} from "@/api/api";
export default {
  name: "doctorTeam",
  data() {
    return {
      doctorList:[],
      docTotalItems:null,
      docPagenum:1,// 分页当前页
      pageCount:null
    }
  },
  methods:{
    docChang(pageNum) {
      this.loadDoctorList(pageNum)
    },
    async loadDoctorList(pageNum=1) {
      let query = {
        pageNum:pageNum,
        pageSize:6
      }
      const res = await getDoctorList(query)
      this.pageCount = res.totalPage
      this.docPagenum = pageNum
      this.doctorList = res.rows
    }
  },
  mounted() {
    this.loadDoctorList()
  }
}
</script>

